@import url(https://fonts.googleapis.com/css?family=Raleway:400,500);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@media only screen and (max-width: 600px){.buttons{flex-direction:column-reverse}.buttons #new-quote{margin-bottom:10px}}.App{text-align:center}.App #quote-box{width:450px;height:auto;padding:40px 50px;background-color:white;border-radius:10px;color:black;font-family:'Raleway', sans-serif;font-weight:400}.App #quote-box #text{margin-bottom:0px}.App #quote-box #text #quote-icon{margin-right:10px}.App #quote-box #author{margin-top:0px;text-align:end}.App #quote-box .buttons{display:flex;justify-content:space-between;min-height:50px}.App #quote-box #tweet-quote{display:flex;align-self:center;padding:10px;color:white;border-radius:10px;justify-content:center}.App #quote-box #new-quote{color:white;border-radius:10px;padding:10px;border:none;cursor:pointer;font-size:16px}.App .App-header{padding:10px;background-color:#282c34;min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);color:white}

